import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"
import Vuex from "vuex"
export default {
	computed: {
		...Vuex.mapState(["detail", "paused", "musicIndex", "history"])
	},
	methods: {
		...Vuex.mapMutations(["play", "pause", "setIndex", "setSrc", "setDetail"]),
		goMusic(){
			uni.navigateTo({
				url: '/pages/music/music'
			})
		},
		next() {
			this.setIndex(1)
			let src = this.history[this.musicIndex].src;
			let detail = this.history[this.musicIndex].detail;
			this.setSrc(src);
			this.setDetail(detail)
			this.paused ? this.play() : this.pause()
		},
		prev() {
			this.setIndex(-1)
			let src = this.history[this.musicIndex].src;
			let detail = this.history[this.musicIndex].detail;
			this.setSrc(src);
			this.setDetail(detail)
			this.paused ? this.play() : this.pause()
		}
	},
	watch: {
	},
	components: {cmdIcon},
}
